	import {
        mapMutations
    } from 'vuex';
    import {Decrypt,Encrypt} from '../../security/cryptojs';
	export default{
		data(){
			return {
				telephone: '1',
				password: '1',
				logining: false,
			    otpCode:"1",
				name:"1",
				gender:"1",
				age:"1",
				form_query:{
                    telephone: '',
                    encrptPassword: '',
                    encrptPassword2: '',
                    otpCode:"",
                    name:"Bnana",
                    gender:"1",
                    age:"1",
				}
			}
		},
		onLoad(){

		},
		methods: {
            ...mapMutations(['login']),
            inputChange(e) {
                const key = e.currentTarget.dataset.key;
                this[key] = e.detail.value;
            },
            navBack() {
                uni.navigateBack();
            },
            navToLogin(){
                var url = '/pages/public/login';
                uni.navigateTo({
                    url
                })
            },
            toRegist() {
                if(!/(^1[3|4|5|7|8][0-9]{9}$)/.test(this.form_query.telephone)){
                    this.$api.msg('请输入正确的手机号码');
                    return;
                }
                if(!/(^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,18}$)/.test(this.form_query.encrptPassword)){
                    this.$api.msg('密码必须为英文加数字且大于6位');
                    return;
                } if(this.form_query.encrptPassword2 == null || this.form_query.encrptPassword2 == "" ){
                    this.$api.msg('请确认密码！');
                    return;
                }
                if(this.form_query.encrptPassword != this.form_query.encrptPassword2){
                    this.$api.msg('密码不一致，请重新输入！');
                    this.form_query.encrptPassword = ""
                    this.form_query.encrptPassword2 = ""
                    return;
                }
                //this.form_query.telephone = Encrypt(this.form_query.telephone)
                this.form_query.encrptPassword = Encrypt(this.form_query.encrptPassword)
                var params = this.form_query

                this.$http.post("user/register", params).then((res) => {
                    console.log("success")
                    console.log(res)
                    if (res.data.status == "fail") {
                        this.$api.msg(res.data.data.errMsg);
                        this.form_query = {}
                    }else {
                        this.$api.msg("注册成功！请登录");
                        this.navToLogin()
                    }
                }).catch(error => {
                    console.log(error)
                    return
                }).finally(() => {

                })
            },
            jsonToStr(json) {
                let str = "";
                for (let key in json) {
                    str += "&" + key + "=" + json[key];
                }
                return str.substring(1);
            },
            async toLogin() {
                this.logining = true;
                const {mobile, password} = this;
